body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  width: 100%;
  background-color: #d1d0bb;
  min-height: 100vh;
  height: auto;
}

#logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40px;
  pointer-events: none;
}

.topTitle {
  height: 11vh;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  background-color: #c5a901;
}

.allButtons {
    box-sizing: border-box;
    color: #5c4925;
    letter-spacing: normal;
    text-transform: none;
    border-radius: 6px;
    background: #fff;
    font-size: 16px;
    border-color: #5c4925;
    border-width: 2px;
    border-style: solid;
    padding: .6em 1em;
}

.allButtons:hover,
.allButtons:focus {
  color: white;
  background-color: #5c4925;
  outline: none;
}

.links {
  text-decoration: none;
  color: inherit;
}

.links, links:hover, links:focus {
  outline: none;
}

#saveButton {
  background-color: #d1d0bb;
}

#saveButton:hover,
#saveButton:focus {
  background-color: #5c4925;
  outline: none;
}

#transferButton {
  background-color: #d1d0bb;
}

#transferButton:hover,
#transferButton:focus {
  background-color: #5c4925;
  outline: none;
}

#brewMain {
  height: 100%;
}

#startBatch {
  padding-top: 32px;
  margin-bottom: 32px;
  text-align: center;
}

.batchValue {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

#batchEnter {
  margin-top: 8px;
  text-align: center
}

h2 {
  margin: 0;
  padding-top: 16px;
  text-align: center;
  font-family: 'Rakkas', cursive;
  font-weight: normal;
  font-size: 30px;
  color:  black;
}

.userInputs {
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 32px;
  padding-bottom: 32px;
  border: 2px solid #282c34;
  border-radius: 6px;
  background-color: white;
}

.cFermenter {
  margin-top: 16px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
  border: 2px solid #5c4925;
  width: 250px;
  height: 250px;
}

.TankNumber {
  color: #5c4925;
  background-color: white;
  margin-left: 30%;
  margin-right: 30%;
  margin-top: 16px;
  margin-bottom: 16px;
  width: 65px;
  height: 30px;
  border-radius: 6px;
  letter-spacing: normal;
  border: 2px solid #5c4925;
}

.TankNumber:hover,
.TankNumber:active {
  background-color: #5c4925;
  color: white;
  outline: none;
}

#fermenterVal {
  margin-top:0;
  margin-bottom: 0;
}

.fermenterData, h4 {
  margin: 0;
}

#fermentationBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

#fermentationFormBox {
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

#fermentationForm {
  margin-right: 50px;
  margin-top: 60px;
  min-width: 300px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  margin-bottom: 20px;
}

#fermentationVisuals {
  width: auto;
  height: 600px;
  padding-top: 64px;
}

.fermentationData {
  border-bottom: 0.5px solid #5c4925;
  padding-bottom: 10px;
  min-height: 100px;
  padding-left: 10px;
  padding-right: 10px;
}

#fermentationSave {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border-bottom: none;
}

.organizeFermentation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (max-width: 1000px) {
  #fermentationBox {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center; 
    padding-right: 0;
    width:auto
  }

  #fermentationForm {
    min-width: 275px;
    margin-left: 32px;
    margin-right: 32px;
  }

  #fermentationVisuals {
    height: auto;
    width: auto;
  }
}

@media (max-width: 550px) {
  

  #fermentationVisuals {
    height: auto;
    width: auto;
  }
}

